.factsheetContent {
  white-space: normal;
  word-break: break-all;
  /* padding-top: 10px; */
  text-align: center;
  /* width: 95%; */
  margin-right: auto;
  margin-left: auto;
}
.factsheetContent h1 {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.factsheetContent .factsheetSubTitle {
  text-align: center;
  color: black;
  font-size: 18px;
  margin-top: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.factsheetContainer {
  overflow: hidden;
  /* font-family: sans-serif, Arial, Helvetica; */
}
.factsheetTitle {
  text-align: center;
  font-size: 2.2em;
  color: black;
}
.factsheetSlot {
  position: relative;
  display: inline-block;
  border: 0.5px solid #919191;
  vertical-align: top;
  /* height: 235px; */
  height: 200px;
  margin: 8px;
  border-radius: 1px 1px 1px 1px;
  background-color: white;
  padding-bottom: 5px;
  overflow: hidden;
}
.factsheetSlotIn {
  float: left;
  padding-left: 0.2%;
  padding-top: 0.2%;
  margin-top: 10px;
  width: 100%;
}
.factsheetDes {
  width: 30%;
  padding-left: 3%;
  text-align: left;
  font-size: 5%;
  font-family: Arial, Helvetica, sans-serif;
}
.factsheetSlotType {
  display: block;
  /* position: absolute;
    top: 10px;
    left: 15px; */
  font-family: "Trebuchet MS", Helvetica, sans-serif;
  color: black;
  text-align: left;
  margin-left: 10px;
}
.factsheetSlotId {
  display: inline;
  position: absolute;
  bottom: 5px;
  right: 15px;
}
.factsheetDes {
  color: black;
}
.factsheetSlotContent {
  /* margin-top: 5px */
  /* height: calc(100% - 21px); */
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.factsheetSlot_2p3 {
  width: calc((90% - 16px) / 3);
}
.factsheetSlot_6p {
  width: calc(90% + 16px);
}
.factsheetSlot_2p {
  width: calc(90% / 3 - 16px / 3);
}
.factsheetSlot_4p {
  width: calc(90% / 3 * 2 + 16px / 3);
}
.factsheetSlot_3p {
  width: calc(90% / 2);
}
.factsheetSlot_4p2 {
  width: calc(90% / 4 - 8px);
}
.factsheetSlot_5p {
  width: calc(90% / 5 - 9px);
}
/* 三个并列 */
.factsheetSlot_2p3 .factsheetSlotIn {
  /* float: none;
    margin-left: auto;
    margin-right: auto; */
  float: none;
  display: inline-block;
  vertical-align: middle;
  margin-top: 20px;
}
.factsheetSlot_2p3 .factsheetSlotIn .factsheetSlotContent {
  display: flex;
  justify-content: space-between;
}
.factsheetSlot_2p3 .factsheetSlotIn .factsheetDes {
  width: 45%;
  padding-left: 10px;
  align-self: center;
  font-size: 8px;
  margin-top: 20px;
}
.factsheetSlot_2p3 .factsheetSlotIn .factsheetVis {
  /* flex: 0 1 auto; */
  align-self: center;
  margin-top: 0px;
}
