.text-uppercase {
  text-transform: uppercase;
  font-weight: bold;
}
.tools {
  position: absolute;
  top: 4px !important;
  left: 150px !important;
  z-index: 99;
  margin: 5px;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
.tools > *:first-child {
  margin-right: 5px;
}
.tools > .ant-radio-button-wrapper > span > i {
  color: #6D757A;
}
@media only screen and (max-width: 414px) {
  .tools {
    position: absolute;
    top: -2px !important;
    left: 100px !important;
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
  }
}
@media only screen and (min-width: 414px) and (max-width: 675px) {
  .tools {
    position: absolute;
    top: -2px !important;
    left: 100px !important;
    z-index: 99;
    margin: 5px;
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
  }
}
